
import type { CommonPackage} from './common';
import type { v3Env, LoggerSender } from '@/config/types';

import { BatchLog as BaseLogger } from '@/core/logger';
import sendLog, { get } from '@/io/xhr';
import { dataTrackHost, Apis, buildUrl } from '@/config/apis';
import { getKrnWebloggerSwitch } from './krn';
import { getReportUrl } from '@/util/test';
/**
 * env 为 logger 时上报的 url
 */
const getLoggerReportUrl = (did: string = 'krnlogger') => {
    get(`${dataTrackHost}/rest/${did}`);
    return getReportUrl(dataTrackHost, `${dataTrackHost}/rest`, did);
};

export default class Logger extends BaseLogger {
    override radarUrl: string = '';
    override baseSendData: LoggerSender = sendLog;
    constructor(config: any, commonPackage: CommonPackage) {
        super(config, commonPackage);
    }
    /**
     * 根据配置生成上报的 url 地址
     */
    override updateUrls() {
        const { env, logger } = this.config;
        this.getKrnWebloggerSwitch();
        if (logger || env === 'logger') {
            const loggerEnv = getLoggerReportUrl(this.commonPackage.identity_package.device_id);
            this.url = buildUrl(loggerEnv);
        } else if (env && /^(https?:)?\/\//.test(env)) {
            this.url = env;
        } else {
            this.url = buildUrl(env as v3Env);
            this.radarUrl = buildUrl(env as v3Env, true);
        }
        this.formatUrl();
    }
    async getKrnWebloggerSwitch() {
        const url = await getKrnWebloggerSwitch();
        if (url) {
            this.config.logger = true;
            this.url = buildUrl(url);
            this.radarUrl = this.url.replace(this.url.indexOf(Apis.v2) !== -1 ? Apis.v2 : Apis.v3, Apis.radar);
        }
    }
}