import { NativeModules, AppState, Platform } from 'react-native';

export interface KrnDeviceInfo {
    appVersion: string;
    locale: string;
    uuid: string;
    systemVersion: string;
    networkType: string;
    manufacturer: string;
    model: string;
    screenWidth: number;
    screenHeight: number;
    statusBarHeight: number;
    titleBarHeight: number;
    os: string;
    osVersion: string;
    androidId?: string;
    product_name?: string;
    idfa?: string;
    imei?: string;
    globalId?: string;
};
export interface KrnPageInfo {
    RNVersion?: string;
    SDKVersion?: string;
    BundleId?: string;
    BundleVersion?: string;
    BundleVersionCode?: string;
    ComponentName?: string;
    TaskId?: string; // keep 发布的 task id
}

export interface KrnLoggerDebug {
    host: string;
    duration: number;
}

export interface KrnPlatform {
    OS: string;
    Version: number;
}

const Krn: {
    NativeModules: any;
    AppState: any;
    deviceInfo: KrnDeviceInfo;
    Platform: typeof Platform;
} = {
    NativeModules,
    AppState,
    deviceInfo: getKrnDeviceInfo(),
    Platform
};

/**
 * 获取 krn 的 page 信息
 * @param rootTag
 * @returns
 */
export const getCurrentPage = (rootTag: number): Promise<KrnPageInfo | {}> => {
    return new Promise(resolve => {
        try {
            // debug 模式下获取不到页面信息
            if (NativeModules?.RootViewPageBridge?.rootViewSignatureWith && typeof rootTag === 'number') {
                NativeModules.RootViewPageBridge.rootViewSignatureWith(rootTag, (data: any) => {
                    const resultData = typeof data === 'string' ? JSON.parse(data) : data;
                    resolve({
                        ...resultData,
                    });
                });
            } else {
                resolve({});
            }
        } catch (err) {
            resolve({});
        }
    });
};
/**
 * 获取端内用户 id
 * @returns
 */
export const getUserId = (): Promise<string> => {
    return new Promise(resolve => {
        try {
            if (NativeModules?.KSUserInfo) {
                NativeModules.KSUserInfo.getUserInfo((res: any) => {
                    resolve(res && res.userId);
                });
            } else {
                resolve('');
            }
        } catch (err) {
            resolve('');
        }
    })
}

/**
 * 获取 krn 设备信息
 */
function getKrnDeviceInfo(): KrnDeviceInfo {
    // ios/android 8.2.20版本新增
    const deviceInfo = NativeModules?.KSRCTBridge?.deviceInfo || NativeModules?.KRNBasic?.deviceInfo;
    if (deviceInfo) {
        const dataJSON = typeof deviceInfo === 'string' ? JSON.parse(deviceInfo) : deviceInfo;
        const { data, result } = dataJSON;
        if (result === 1) {
            const os = Platform.OS;
            const osVersion = Platform.Version;
            return {
                ...data,
                os,
                osVersion
            };
        }
    }
    return {
        appVersion: '',
        locale: '',
        uuid: '',
        systemVersion: '',
        networkType: '',
        manufacturer: '',
        model: '',
        screenWidth: 0,
        screenHeight: 0,
        statusBarHeight: 0,
        titleBarHeight: 0,
        os: '',
        osVersion: '',
    };
}

/**
 * 通过bridge获取值切换发送地址
 */
export async function getKrnWebloggerSwitch() {
    const NativeModules = Krn.NativeModules;
    try {
        const res = await NativeModules.WebLoggerDebugger?.getLoggerDebugConfig();
        if (typeof res === 'string') {
            const { host = '' } = JSON.parse(res);
            if (!host) return;
            const hostArr = host.match(/([^\/]+)/g) || [];
            const loggerSessionId = hostArr[hostArr.length - 1] || '';
            if (!loggerSessionId) {
                return '';
            }
            // const reportHost = host.split(loggerSessionId)[0] || '';
            // get(`${reportHost}/${loggerSessionId}`);
            return host;
        }
        return '';
    } catch {
        return '';
    }
}

export default Krn;