import type { UrlMapFn, UrlMap, PageTypeKey, UrlObj } from '@/config/types';
import { getMappingPage, getUrlAndQueryFromUrl } from '@/util';
import BaseUrlPackage from '@/core/url-package';
import { uuid } from '@/util/uuid';
export class UrlPackage extends BaseUrlPackage {
    constructor(url: string | UrlObj, pageType: PageTypeKey, urlMap?: UrlMap | UrlMapFn) {
        super({}, pageType);
        // rn 里实际上是没有 url 的，但是因为有些页面使用的组件要一码多端用，所以这里加上兼容
        if (typeof url === 'string') {
            const obj = getUrlAndQueryFromUrl(url);
            this.page = obj.page;
            this.params = obj.params;
        } else {
            this.page = (url || {}).page || '';
            this.params = (url || {}).params || undefined;
        }
        this.identity = this.generatePageId();
        if (urlMap && typeof url === 'string') {
            this.init(url, urlMap);
        }
    }
    override generatePageId() {
        return uuid();
    }
    init(url: string, urlMap: UrlMap | UrlMapFn = {}) {
        const { page, params } = getMappingPage({url, page: this.page, params: this.params}, urlMap);
        this.update(page, params);
    }
}