import type { CommonPackageOptions } from '@/core/common';
import type { Base } from '@/proto/base';

import { CommonPackage as BaseCommonPackage } from '@/core/common';
import { networkType } from '@/config/types';
import Krn, { getUserId } from './krn';


export class CommonPackage extends BaseCommonPackage {
    additional_seq_id_package?: Base.AdditionalSeqIdPackage;
    constructor(options: CommonPackageOptions) {
        super({
            identity_package: {
                device_id: Krn.deviceInfo.uuid,
                global_id: Krn.deviceInfo.globalId,
            },
            app_package: {
                language: Krn.deviceInfo.locale,
                platform:  Krn.Platform.OS === 'ios' && 3
                    || Krn.Platform.OS === 'android' && 1
                    || Krn.Platform.OS === 'web' && 10 || 0,
                container: 'REACT_NATIVE',
                version_name: Krn.deviceInfo.appVersion,  // 宿主客户端版本号
            },
            device_package: {
                os_version: String(Krn.Platform.Version) || 'unknown',
                model: Krn.deviceInfo.manufacturer || Krn.deviceInfo.model
            },
            network_package: {
                type: Krn.deviceInfo.networkType === 'WIFI' ? networkType.wifi : networkType.unknown,
            },
            ...options
        });
        const { product_name, screenWidth, screenHeight } = Krn.deviceInfo;
        Object.assign(this.h5_extra_attr, {
            host_product: product_name,
            resolution: `${screenWidth}x${screenHeight}`
        }, options.h5_extra_attr);
        if (!this.identity_package.user_id) {
            getUserId().then(userId => {
                Object.assign(this.identity_package, {
                    user_id: userId
                });
            });
        }
    }
}